"use client";

import React, { useState } from "react";
import { Calendar, momentLocalizer, Views } from "react-big-calendar";
import moment from "moment";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Clock, Calendar as CalendarIcon, User, Plus } from "lucide-react";
import { Button } from "@/components/ui/button";
import "react-big-calendar/lib/css/react-big-calendar.css";

// Setup the localizer for Big Calendar
const localizer = momentLocalizer(moment);

const Dashboard = ({ appointments = [], userType = "doctor" }) => {
  const [view, setView] = useState(Views.MONTH);
  const [date, setDate] = useState(new Date());
  
  // Format appointments for Big Calendar
  const formattedAppointments = appointments.map(app => {
    const startDate = new Date(app.date);
    const [hours, minutes] = app.time.split(":").map(Number);
    startDate.setHours(hours, minutes, 0);
    
    // Create end time (1 hour after start time)
    const endDate = new Date(startDate);
    endDate.setHours(endDate.getHours() + 1);
    
    return {
      ...app,
      start: startDate,
      end: endDate,
      title: app.title,
    };
  });
  
  // Filter appointments for the selected date (ignoring time)
  const appointmentsForDate = appointments.filter((app) => {
    return moment(new Date(app.date)).format("YYYY-MM-DD") === moment(date).format("YYYY-MM-DD");
  });

  // Total appointments and unique doctor names
  const totalAppointments = appointments.length;
  const doctorNames = [...new Set(appointments.map((app) => app.doctorName))];

  // Custom event components for the calendar
  const EventComponent = ({ event }) => (
    <div className="text-xs p-1">
      <div className="font-bold truncate">{event.title}</div>
      <div>{event.time}</div>
    </div>
  );

  return (
    <div className="flex h-screen bg-gray-50">
      {/* Sidebar - 20% width */}
      <div className="w-1/5 p-4 border-r border-gray-200 bg-white overflow-y-auto">
        <h1 className="text-xl font-bold mb-6 text-gray-800">Your Dashboard</h1>
        
        {/* Stats Cards */}
        <Card className="mb-4">
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-700 flex items-center">
              <CalendarIcon className="h-4 w-4 mr-2 text-blue-500" />
              Total Appointments
            </CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-2xl font-bold text-blue-600">{totalAppointments}</p>
            <p className="text-xs text-gray-500 mt-1">
              {userType === "doctor" ? "scheduled with patients" : "with healthcare providers"}
            </p>
          </CardContent>
        </Card>
        
        <Card className="mb-4">
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-700 flex items-center">
              <User className="h-4 w-4 mr-2 text-indigo-500" />
              {userType === "doctor" ? "Patients" : "Your Doctors"}
            </CardTitle>
          </CardHeader>
          <CardContent>
            {doctorNames.length > 0 ? (
              <div className="flex flex-wrap gap-1">
                {doctorNames.map((name, idx) => (
                  <Badge key={idx} variant="outline" className="bg-indigo-50 text-indigo-700 border-indigo-200 text-xs">
                    {name}
                  </Badge>
                ))}
              </div>
            ) : (
              <p className="text-xs text-gray-500">No appointments scheduled</p>
            )}
          </CardContent>
        </Card>
        
        {/* Daily Appointments Section */}
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium flex items-center">
              <Clock className="h-4 w-4 mr-2 text-green-500" />
              <span>{moment(date).format("MMMM D, YYYY")}</span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            {appointmentsForDate.length > 0 ? (
              <div className="space-y-2">
                {appointmentsForDate.map((app) => (
                  <div key={app.id} className="p-2 rounded-lg bg-white border border-gray-200 shadow-sm hover:shadow-md transition-shadow">
                    <h4 className="font-medium text-gray-800 text-sm">{app.title}</h4>
                    <div className="flex items-center text-gray-500 text-xs mt-1">
                      <Clock className="h-3 w-3 mr-1" />
                      <span>{app.time}</span>
                    </div>
                    <div className="flex items-center text-gray-500 text-xs mt-1">
                      <User className="h-3 w-3 mr-1" />
                      <span>{app.doctorName}</span>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="flex flex-col items-center justify-center h-28 text-center">
                <CalendarIcon className="h-8 w-8 text-gray-300 mb-2" />
                <p className="text-gray-500 text-xs">No appointments for this day</p>
              </div>
            )}
          </CardContent>
        </Card>
        
        <Button className="w-full mt-4 bg-blue-600 hover:bg-blue-700">
          <Plus className="h-4 w-4 mr-1" /> New Appointment
        </Button>
      </div>
      
      {/* Main Calendar - 80% width */}
      <div className="w-4/5 p-4 overflow-hidden">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-2xl font-bold text-gray-800">Appointment Calendar</h2>
          
          <Tabs defaultValue="month" className="w-auto" onValueChange={(val) => {
            switch(val) {
              case "month": setView(Views.MONTH); break;
              case "week": setView(Views.WEEK); break;
              case "day": setView(Views.DAY); break;
              case "agenda": setView(Views.AGENDA); break;
            }
          }}>
            <TabsList>
              <TabsTrigger value="month">Month</TabsTrigger>
              <TabsTrigger value="week">Week</TabsTrigger>
              <TabsTrigger value="day">Day</TabsTrigger>
              <TabsTrigger value="agenda">Agenda</TabsTrigger>
            </TabsList>
          </Tabs>
        </div>
        
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 h-full">
          <Calendar
            localizer={localizer}
            events={formattedAppointments}
            startAccessor="start"
            endAccessor="end"
            view={view}
            onView={setView}
            date={date}
            onNavigate={setDate}
            style={{ height: "calc(100vh - 160px)" }}
            components={{
              event: EventComponent
            }}
            eventPropGetter={(event) => {
              return {
                style: {
                  backgroundColor: '#3b82f6',
                  borderRadius: '4px',
                  color: 'white',
                  border: 'none',
                }
              };
            }}
            dayPropGetter={(date) => {
              const today = new Date();
              if (moment(date).format("YYYY-MM-DD") === moment(today).format("YYYY-MM-DD")) {
                return {
                  style: {
                    backgroundColor: '#f0f9ff',
                  }
                };
              }
              return {};
            }}
            onSelectEvent={(event) => {
              setDate(event.start);
            }}
          />
        </div>
      </div>
    </div>
  );
};

export default Dashboard;